<template>
  <div>
    <van-nav-bar title="登录" left-arrow @click-left="onClickLeft" />
    <!--photo-->
    <van-cell-group class="photo">
      <van-image round width="100" height="100" src="https://img01.yzcdn.cn/vant/cat.jpeg" />
    </van-cell-group>
    <!--main-->
    <van-cell-group>
      <van-form @submit="onSubmit">
        <van-field v-model="username" name="用户名" label="用户名" placeholder="用户名"
          :rules="[{ required: true, message: '请填写用户名' }]" />


        <van-field v-model="password" type="password" name="密码" label="密码" placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]" />

        <van-field v-model="sms" center clearable label="短信验证码" placeholder="请输入短信验证码">
          <template #button>
            <van-button size="small" type="primary">发送验证码</van-button>
          </template>
        </van-field>


        <div style="margin: 16px;">
          <van-button round block type="info" native-type="submit">提交</van-button>
        </div>
      </van-form>
    </van-cell-group>

  </div>

  </div>
</template>

<script>
  import {
    login
  } from '../../common/api/web/user.js'
  //import qs from 'qs'
  export default {
    data() {
      return {
        username: '',
        password: '',
        sms:'',
      };
    },
    methods: {
      onSubmit() {
        let params = {
          name: this.username,
          password: this.password
        }
        let that = this;
        //登录接口
        login(params).then(function(response) {
          if (response.code == 200) {
            localStorage.setItem('token', response.data.token); //生成登录本地参数
            setTimeout(that.$router.push('/index'), 2000);
          }
          that.$toast(response.msg);

        });
        /* console.log(this.username);
         //GET拼接方法数据大小有限
         let host='http://shop.ueoa.net/manage/login?name='+this.username+'&password='+this.password;
         let data={
           name:this.username,
           password:this.password
         }
         //qs.stringify(data) 数据转换方法
         console.log(qs.stringify(data));
         //封装
         this.$http.get(host).then(res => {
           if(res.data.code==200){
             localStorage.setItem('token',res.data.data.token);
             setTimeout(this.$router.push('/index'),2000);
           }
          this.$toast(res.data.msg);
         });
         */



      },
      onClickLeft() {

      }

    }
  }
</script>

<style>
  .photo {
    width: 100%;
    height: auto;
    text-align: center;
    margin: 1.25rem 0px;
  }

  .content {}
</style>



